@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.chart_container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 12px;
      background-color: #f4f4f4;
      margin: 12px;
      color: antiquewhite;
      border-radius: 6px;
}

.linechart_container {
    position: relative;
    top: 20px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
}

h2{
    align-self: center;
    margin-top: 100px;
    color: #333;
}
.header {
      display: flex;
      align-items: center;
      justify-content: right;
      margin-top: 25px;
      margin-left: -200px;
      color: #333;
      border-radius: 6px;
      height: 60px;
      font-size: 18px;
      position: absolute;
      top: 0;
      width: 100%
}

.header_select {
      color: #333;
      font-size: 16px;
      margin: 24px;
      padding: 10px;
      background-color: #eee;
      
}
.App {
      font-family: 'Open Sans', sans-serif;
      display: flex;
      /* height: 100%;  */
      flex-direction: column;
      justify-content: center;
      /* background-color: #eeee; */
      position: relative;
}

